.todo-card {
    width: 80%;
    height: auto;
    margin: 0px 10px 0px 10px;
    -webkit-box-shadow: lightgray;
            box-shadow: lightgray;
    border: 1px solid gray;
    border-radius: 3px;
}

.todo-card .top-containner {
    background: lightblue;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
}

.top-containner .title {
    -ms-flex: 3 1 0%;
        flex: 3 1 0%
}

.top-containner .desc {
    -ms-flex: 1 1 0%;
        flex: 1 1 0%;
    text-align: right;
}

.title h4 {
    /*margin: 10px 0px 0px 10px;*/
    margin: 10px;
}

.desc button {
    height: 100%;
}

.desc .well {
    padding: 10px;
}






